import type { ContentChatConference } from './db/types';

export function confereceEnded(
  status: ContentChatConference['status'],
  created_at: string
) {
  const eightHoursInMillis = 8 * 60 * 60 * 1000;
  const timeDifference = new Date().getTime() - new Date(created_at).getTime();

  return status === 'ENDED' || timeDifference > eightHoursInMillis;
}
