<template>
  <button
    class="attachment-list-container"
    :disabled="!!fileData.deleted_at"
    @click="handleDownload(fileData.hash, fileData.filename)"
  >
    <svg
      class="attachment-list-download"
      width="32"
      height="33"
      viewBox="0 0 32 33"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <rect x="0.5" y="1" width="31" height="31" rx="3.5" fill="#0676C5" />
      <path
        d="M12.3945 21.3C12.0643 21.3 11.7826 21.1825 11.5492 20.9475C11.3159 20.7125 11.1992 20.43 11.1992 20.1V18.9H12.3992V20.1H19.5992V18.9H20.7992V20.1C20.7992 20.43 20.6817 20.7125 20.4465 20.9475C20.2114 21.1825 19.9287 21.3 19.5986 21.3H12.3945ZM15.9992 18.9L12.7992 15.7L13.6492 14.85L15.3992 16.6V10.9H16.5992V16.6L18.3492 14.85L19.1992 15.7L15.9992 18.9Z"
        fill="white"
      />
      <rect x="0.5" y="1" width="31" height="31" rx="3.5" stroke="#0676C5" />
    </svg>

    <div class="attachment-list-icon">
      <Component
        :is="
          sourceImage(fileData.name ?? fileData.filename, fileData.deleted_at)
        "
      />
    </div>
    <div class="attachment-list-text">
      <span class="attachments-item-description">{{
        fileData.name ?? fileData.filename
      }}</span>
      <span class="attachment-item-size">{{
        fileData.deleted_at ? 'DELETED' : formatBytes(fileData.size)
      }}</span>
    </div>
  </button>
</template>

<script setup lang="ts">
import FileDelete from '@/components/Attachments/Icons/FileDelete.vue';
import type { FileAttachment } from '@/types';
import { useAttachments, useAttachmentsIcon } from '@/composables';
import { computed } from 'vue';
import { formatBytes } from '@/utils';

const { downloadFile } = useAttachments();
const { findIconByName } = useAttachmentsIcon();

interface Props {
  fileData: FileAttachment;
}

defineProps<Props>();
async function handleDownload(hash: string, filename: string) {
  await downloadFile(hash, filename);
}

const sourceImage = computed(() => {
  return (filename: string, deletedAt: string | undefined) => {
    if (deletedAt) {
      return FileDelete;
    }
    return findIconByName.value(filename);
  };
});
</script>

