<template>
  <div class="mx-1">
    <button class="delete-attachments-details" v-bind="api.triggerProps">
      <IThreeDots />
    </button>
    <Teleport v-if="api.isOpen" to="body">
      <div
        v-bind="api.positionerProps"
        style="z-index: 3"
        class="delete-attachments-main"
      >
        <PerfectScrollbar>
          <div v-bind="api.contentProps" class="delete-attachments-container">
            <template v-for="(file, index) in files" :key="file.hash">
              <div
                class="delete-attachments-item-container"
                @click="emit('delete', file, index)"
              >
                <span class="delete-attachments-item"
                  >{{ t('Delete') }} "{{ file.name ?? file.filename }}"</span
                >
              </div>
            </template>
          </div>
        </PerfectScrollbar>
      </div>
    </Teleport>
  </div>
</template>

<script setup lang="ts">
import * as popover from '@zag-js/popover';
import { normalizeProps, useMachine } from '@zag-js/vue';
import { computed } from 'vue';
import BaseIcon from '@/components/BaseIcon.vue';
import { useI18n } from 'vue-i18n';
import type { FileAttachment } from '@/types';
import IThreeDots from '@/components/Icons/IThreeDots.vue';

interface Emits {
  (e: 'change', value: boolean): void;
  (e: 'delete', file: FileAttachment, id: number): void;
}

interface Props {
  files: FileAttachment[];
  me: boolean;
}

const emit = defineEmits<Emits>();
const props = defineProps<Props>();

const { t } = useI18n();

const [state, send] = useMachine(
  (popover as any).machine({
    id: crypto.randomUUID(),
    positioning: { placement: props.me ? 'top-end' : 'top-start' },
    closeOnEsc: true,
    closeOnInteractOutside: true,
    onOpenChange(details: { open: boolean }) {
      emit('change', details.open);
    },
  })
);
const api = computed(() =>
  popover.connect(state.value as any, send, normalizeProps)
);
</script>

<style scoped>
.delete-attachments-details {
  all: unset;
  cursor: pointer;
  height: 24px;
}

.delete-attachments-main {
  border-radius: 5px;
  border: 1px solid var(--line);
  background: var(--bg-element);
}

.delete-attachments-container {
  width: 280px;
  min-height: 51px;
  max-height: 153px;
  padding: 6px 0;
  gap: 5px;
  display: flex;
  flex-direction: column;
}

.delete-attachments-item {
  display: inline-block;
  max-width: 248px;
  height: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: var(--text-primary);
  font-size: 14px;
  padding: 8px 16px;
  cursor: pointer;
}

.delete-attachments-item-container {
  height: 37px;
}

.delete-attachments-item-container:hover {
  background-color: var(--hover);
}
</style>
