<template>
  <div class="typing-box" :style="{ gap: '11px' }">
    <BaseAvatar :src="srcAvatar" size="1.5rem" :status="statusOnline" />
    <div class="typing-container">
      <svg
        xmlns="http://www.w3.org/2000/svg"
        width="20"
        height="20"
        viewBox="0 0 20 20"
        fill="none"
      >
        <circle cx="2" cy="10" r="2" fill="#344767">
          <animate
            attributeName="cy"
            values="10;12;10"
            dur="1.25s"
            repeatCount="indefinite"
          />
          <animate
            attributeName="opacity"
            values="1;0;1"
            dur="1.25s"
            repeatCount="indefinite"
          />
        </circle>
        <circle cx="10" cy="10" r="2" fill="#344767">
          <animate
            attributeName="cy"
            values="10;12;10"
            dur="1.25s"
            begin="0.35s"
            repeatCount="indefinite"
          />
          <animate
            attributeName="opacity"
            values="1;0;1"
            dur="1.25s"
            begin="0.35s"
            repeatCount="indefinite"
          />
        </circle>
        <circle cx="18" cy="10" r="2" fill="#344767">
          <animate
            attributeName="cy"
            values="10;12;10"
            dur="1.25s"
            begin="0.7s"
            repeatCount="indefinite"
          />
          <animate
            attributeName="opacity"
            values="1;0;1"
            dur="1.25s"
            begin="0.7s"
            repeatCount="indefinite"
          />
        </circle>
      </svg>
    </div>
  </div>
</template>

<script setup lang="ts">
import type { UserStatusEnum } from '@/types';
import BaseAvatar from '@/components/BaseAvatar.vue';
import { useIndexesStore, useStatusStore } from '@/stores';
import { ImageSize } from '@/enums';
import { computed, toRefs } from 'vue';

interface Props {
  userId: number;
}

const props = defineProps<Props>();
const { userId } = toRefs(props);

const indexesStore = useIndexesStore();
const statusStore = useStatusStore();

const srcAvatar = computed(() => {
  return indexesStore.avatars[userId.value][ImageSize.Default];
});

const statusOnline = computed(() => {
  return (
    (statusStore?.status[userId.value]?.status as unknown as UserStatusEnum) ??
    'offline'
  );
});
</script>

