import { computed, ref } from 'vue';
import type { FileAttachment } from '@/types';
import { useAttachmentStore, useGroupMessageStore } from '@/stores';
import { useRoute } from 'vue-router';

export function useAttachmentsComponent() {
  const attachmentStore = useAttachmentStore();
  const groupMessageStore = useGroupMessageStore();
  const route = useRoute();

  const modalDeleteAttachment = ref(false);
  const fileData = ref({
    id: '',
    name: '',
    size: 0,
    type: '',
    lastModified: 0,
  });

  const deleteAttachmentIsOpen = ref(false);
  const deleteAttachmentIsIsVisible = ref(false);

  function setDeleteAttachmentIsOpen(isOpen: boolean) {
    deleteAttachmentIsOpen.value = isOpen;

    if (!isOpen) {
      deleteAttachmentIsIsVisible.value = false;
    }
  }

  function onMouseover() {
    if (deleteAttachmentIsOpen.value) return;

    deleteAttachmentIsIsVisible.value = true;
  }

  function onMouseLeave() {
    if (deleteAttachmentIsOpen.value) return;

    deleteAttachmentIsIsVisible.value = false;
  }

  function handleSelectedAttachment(file: FileAttachment, index: number) {
    modalDeleteAttachment.value = true;

    fileData.value = {
      id: index.toString(10),
      name: file.name ?? file.filename,
      size: file.size,
      type: '',
      lastModified: new Date(file.created_at).getUTCMilliseconds(),
    };
  }

  function handleDeleteAttachment(
    id: string,
    createdAt: string,
    files: FileAttachment[]
  ) {
    const dstUser = Number(route.params.id);
    attachmentStore.deleteAttachment({
      dstUser,
      file: files[Number(id)],
      createdAt,
    });
  }

  function handleGroupDeleteAttachment(
    id: string,
    createdAt: string,
    files: FileAttachment[]
  ) {
    groupMessageStore.deleteGroupAttachment(files[Number(id)], createdAt);
  }

  const filesAttachments = computed(() => {
    return (files: FileAttachment[]) => {
      return files.filter((file) => file.hash);
    };
  });

  return {
    modalDeleteAttachment,
    fileData,
    deleteAttachmentIsIsVisible,
    setDeleteAttachmentIsOpen,
    filesAttachments,
    onMouseover,
    onMouseLeave,
    handleSelectedAttachment,
    handleDeleteAttachment,
    handleGroupDeleteAttachment,
  };
}
